import React, { Component } from 'react';
import { Bar, Doughnut, Pie } from 'react-chartjs-2';
import styles from './DetailReport.module.css';

class DetailReport extends Component {

	handleChangeServiceName = (serviceName) => {
		switch(serviceName){
			case 'mgist':
				return 'MGIST'
			case 'ebell':
				return ''
			case 'accesscontrol':
				return ''
			case 'vitalsensor':
				return ''
			case 'ndoctor':
				return 'N-Doctor'
			case 'anpr':
				return 'ANPR'
				case 'parkingcontrol':
				return ''
			default:
				return serviceName;
		}
	}

	handleChangeEventTypeLabel = (label) => {
		switch(label){
			case 1:
				return ' '
			case 2:
				return ' '
			case 3:
				return ' '
			case 4:
				return ' '
			case 5:
				return '  '
			case 6:
				return '  '
			case 7:
				return '  '
			case 8:
				return '  '
			case 9:
				return ' '
			case 10:
				return ' '
			case 11:
				return '  '
			case 12:
				return ' '
			case 13:
				return '  '
			case 14:
				return ' '
			case 15:
				return ' '
			case 16:
				return '  '
			case 17:
				return '  '
			case 18:
				return ' '
			case 19:
				return ' '
			case 20:
				return ' ON'
			case 21:
				return ' OFF'
			case 22:
				return ' RESET'
			case 23:
				return ''
			case 24:
				return ''
			case 25:
				return ''
			case 26:
				return ''
			case 27:
				return ''
			case 28:
				return ''
			case 29:
				return 'PIDS  '
			case 30:
				return ' '
			case 31:
				return ' '
			case 32:
				return '  '
			case 33:
				return ' '
			case 34:
				return ' '
			case 35:
				return ' '
			case 36:
				return ''
		}
	}

	handleUnAckRate = (unAcknowledgedData) => {
		const totalCount = unAcknowledgedData.length > 1 ? unAcknowledgedData.reduce((prev, curr) => parseInt(prev.count) + parseInt(curr.count)) : (unAcknowledgedData[0] ? unAcknowledgedData[0].count : '');
		const unAckCount = unAcknowledgedData.length > 1 ? unAcknowledgedData.filter((item) => item.acknowledge === false)[0].count: 0;
		return parseInt((unAckCount/totalCount)*100)
	}

	render () {

	const	eventTypeData = {
		labels: this.props.eventTypeData ? this.props.eventTypeData.map(data => this.handleChangeEventTypeLabel(data.event_type)) : [],
		datasets: [{
			// label: this.handleChangeServiceName(this.props.service),
			data: this.props.eventTypeData ? this.props.eventTypeData.map(data => data.count) : [],
			backgroundColor: [
				'rgba(54, 162, 235, 0.6)',
				'rgba(255, 206, 86, 0.6)',
				'rgba(153, 102, 255, 0.6)',
				'rgba(255, 159, 64, 0.6)',
				'rgba(75, 192, 192, 0.6)',
				'rgba(255, 99, 132, 0.6)',
				'rgba(175, 89, 134, 0.6)',
				'rgba(215, 199, 34, 0.6)',
				'rgba(39, 149, 75, 0.6)',
				'rgba(8, 41, 115, 0.6)',
				'rgba(236, 41, 75, 0.6)',
				'rgba(136, 141, 92, 0.6)',
				'rgba(16, 211, 92, 0.6)',
				'rgba(46, 11, 192, 0.6)',
				'rgba(196, 11, 192, 0.6)',
				'rgba(86, 111, 42, 0.6)',
				'rgba(26, 251, 72, 0.6)',
			],
			borderColor: [
				'rgba(54, 162, 235, 1)',
				'rgba(255, 206, 86, 1)',
				'rgba(153, 102, 255, 1)',
				'rgba(255, 159, 64, 1)',
				'rgba(75, 192, 192, 1)',
				'rgba(255, 99, 132, 1)',
				'rgba(175, 89, 134, 1)',
				'rgba(215, 199, 34, 1)',
				'rgba(39, 149, 75, 1)',
				'rgba(8, 41, 115, 1)',
				'rgba(236, 41, 75, 1)',
				'rgba(136, 141, 92, 1)',
				'rgba(16, 211, 92, 1)',
				'rgba(46, 11, 192, 1)',
				'rgba(196, 11, 192, 1)',
				'rgba(86, 111, 42, 1)',
				'rgba(26, 251, 72, 1)',
			],
			borderWidth: 1,
			fill: false
		}]
	};

	const eventTypeOptions = {
		scales: {
			yAxes: [{
				ticks: {
					beginAtZero: true,
					fontSize: 15,
					stepSize: 1
				},
				gridLines: {
					color: "rgba(204, 204, 204,0.1)"
				}
			}],
			xAxes: [{
				gridLines: {
					color: "rgba(204, 204, 204,0.1)"
				},
				ticks: {
					fontSize: 15,
					color: "rgba(204, 204, 204,0.9)"
				}
			}]
		},
		legend: {
			display: false,
			labels: {
				fontSize: 15
			}
		},
		elements: {
			point: {
				radius: 0
			}
		},
		tooltips: {
			titleFontSize: 15,
			bodyFontSize: 15,
			intersect: false
		}
	}

	const	severityData = {
		datasets: [{
			data: this.props.severityData ? this.props.severityData.sort((a, b) => a.event_type_severity - b.event_type_severity).map(data => data.count) : [],
			backgroundColor: this.props.severityData.length === 4 ?
				[
					'rgba(54, 162, 235, 0.8)',
					'rgba(29, 219, 22, 0.8)',
					'rgba(255, 206, 86, 0.8)',
					'rgba(255, 99, 132, 0.8)',
				]
				:
				(
					this.props.severityData.length === 3 ?
						(
							this.props.severityData.every((severityItem) => severityItem.event_type_severity !== 3) ?
								[
									'rgba(54, 162, 235, 0.8)',
									'rgba(29, 219, 22, 0.8)',
									'rgba(255, 206, 86, 0.8)',
								]
								:
								(
									this.props.severityData.every((severityItem) => severityItem.event_type_severity !== 2) ?
										[
											'rgba(54, 162, 235, 0.8)',
											'rgba(29, 219, 22, 0.8)',
											'rgba(255, 99, 132, 0.8)',
										]
									:
									(
										this.props.severityData.every((severityItem) => severityItem.event_type_severity !== 1) ?
											[
												'rgba(54, 162, 235, 0.8)',
												'rgba(255, 206, 86, 0.8)',
												'rgba(255, 99, 132, 0.8)',
											]
										:
										(
											[
												'rgba(29, 219, 22, 0.8)',
												'rgba(255, 206, 86, 0.8)',
												'rgba(255, 99, 132, 0.8)',
											]
										)
									)
								)
						)
						:
						this.props.severityData.length === 2?
							(
								this.props.severityData.some((severityItem) => severityItem.event_type_severity === 0) && this.props.severityData.some((severityItem) => severityItem.event_type_severity === 1)?
									[
										'rgba(54, 162, 235, 0.8)',
										'rgba(29, 219, 22, 0.8)',
									]
									:
									(
										this.props.severityData.some((severityItem) => severityItem.event_type_severity === 0) && this.props.severityData.some((severityItem) => severityItem.event_type_severity === 2)?
											[
												'rgba(54, 162, 235, 0.8)',
												'rgba(255, 206, 86, 0.8)',
											]
											:
											(
												this.props.severityData.some((severityItem) => severityItem.event_type_severity === 0) && this.props.severityData.some((severityItem) => severityItem.event_type_severity === 3)?
													[
														'rgba(54, 162, 235, 0.8)',
														'rgba(255, 99, 132, 0.8)',
													]
													:
													(
														this.props.severityData.some((severityItem) => severityItem.event_type_severity === 1) && this.props.severityData.some((severityItem) => severityItem.event_type_severity === 2)?
															[
																'rgba(29, 219, 22, 0.8)',
																'rgba(255, 206, 86, 0.8)',
															]
															:
															(
																this.props.severityData.some((severityItem) => severityItem.event_type_severity === 1) && this.props.severityData.some((severityItem) => severityItem.event_type_severity === 3)?
																	[
																		'rgba(29, 219, 22, 0.8)',
																		'rgba(255, 99, 132, 0.8)',
																	]
																	:
																	[
																		'rgba(255, 206, 86, 0.8)',
																		'rgba(255, 99, 132, 0.8)',
																	]
															)
													)
											)
									)	
							)
							:
							this.props.severityData.length === 1 ?
								(
									this.props.severityData.find((severityItem) => severityItem.event_type_severity === 0) ?
										[
											'rgba(54, 162, 235, 0.8)',
										]
										:
										(
											this.props.severityData.find((severityItem) => severityItem.event_type_severity === 1) ?
												[
													'rgba(29, 219, 22, 0.8)',
												]
											:
											(
												this.props.severityData.find((severityItem) => severityItem.event_type_severity === 2) ?
													[
														'rgba(255, 206, 86, 0.8)',
													]
													:
													[
														'rgba(255, 99, 132, 0.8)',
													]
											)
										)
									)
									:
									[]
				)
			,
			borderColor: this.props.severityData.length === 4 ?
				[
					'rgba(54, 162, 235, 1)',
					'rgba(29, 219, 22, 1)',
					'rgba(255, 206, 86, 1)',
					'rgba(255, 99, 132, 1)',
				]
				:
				(
					this.props.severityData.length === 3 ?
						(
							this.props.severityData.every((severityItem) => severityItem.event_type_severity !== 3) ?
								[
									'rgba(54, 162, 235, 1)',
									'rgba(29, 219, 22, 1)',
									'rgba(255, 206, 86, 1)',
								]
								:
								(
									this.props.severityData.every((severityItem) => severityItem.event_type_severity !== 2) ?
										[
											'rgba(54, 162, 235, 1)',
											'rgba(29, 219, 22, 1)',
											'rgba(255, 99, 132, 1)',
										]
										:
										(
											this.props.severityData.every((severityItem) => severityItem.event_type_severity !== 1) ?
												[
													'rgba(54, 162, 235, 1)',
													'rgba(255, 206, 86, 1)',
													'rgba(255, 99, 132, 1)',
												]
												:
												(
													[
													'rgba(29, 219, 22, 1)',
													'rgba(255, 206, 86, 1)',
													'rgba(255, 99, 132, 1)',
													]
												)
										)
								)
						)
						:
						this.props.severityData.length === 2?
							(
								this.props.severityData.some((severityItem) => severityItem.event_type_severity === 0) && this.props.severityData.some((severityItem) => severityItem.event_type_severity === 1)?
									[
										'rgba(54, 162, 235, 1)',
										'rgba(29, 219, 22, 1)',
									]
									:
									(
										this.props.severityData.some((severityItem) => severityItem.event_type_severity === 0) && this.props.severityData.some((severityItem) => severityItem.event_type_severity === 2)?
											[
												'rgba(54, 162, 235, 1)',
												'rgba(255, 206, 86, 1)',
											]
											:
											(
												this.props.severityData.some((severityItem) => severityItem.event_type_severity === 0) && this.props.severityData.some((severityItem) => severityItem.event_type_severity === 3)?
													[
														'rgba(54, 162, 235,	1)',
														'rgba(255, 99, 132, 1)',
													]
													:
													(
														this.props.severityData.some((severityItem) => severityItem.event_type_severity === 1) && this.props.severityData.some((severityItem) => severityItem.event_type_severity === 2)?
															[
																'rgba(29, 219, 22, 1)',
																'rgba(255, 206, 86, 1)',
															]
															:
															(
																this.props.severityData.some((severityItem) => severityItem.event_type_severity === 1) && this.props.severityData.some((severityItem) => severityItem.event_type_severity === 3)?
																	[
																		'rgba(29, 219, 22, 1)',
																		'rgba(255, 99, 132, 1)',
																	]
																	:
																	[
																		'rgba(255, 206, 86, 1)',
																		'rgba(255, 99, 132, 1)',
																	]
															)
													)
											)
									)	
							)
							:
								this.props.severityData.length === 1 ?
									(
										this.props.severityData.find((severityItem) => severityItem.event_type_severity === 0) ?
											[
												'rgba(54, 162, 235, 1)',
											]
											:
											(
												this.props.severityData.find((severityItem) => severityItem.event_type_severity === 1) ?
													[
														'rgba(29, 219, 22, 1)',
													]
													:
													(
														this.props.severityData.find((severityItem) => severityItem.event_type_severity === 2) ?
															[
																'rgba(255, 206, 86, 1)',
															]
															:
															[
																'rgba(255, 99, 132,	1)',
															]
													)
											)
									)
									:
									[]
				),
			}],
		// These labels appear in the legend and in the tooltips when hovering different arcs
		labels: this.props.severityData.length > 0 ? this.props.severityData.map(data => data.event_type_severity === 0 ? 'Info':(data.event_type_severity === 1 ? 'Minor': (data.event_type_severity === 2 ? 'Major' : 'Critical'))) : []
	};
	
	const severityOptions = {
			responsive: true,
			animation: {
				animateScale: true,
				animateRotate: true
			},
			tooltips: {
				titleFontSize: 15,
				bodyFontSize: 15,
				intersect: false
			},
			legend: {
				display: true,
				labels: {
					fontSize: 15,
					boxWidth: 25
				},
				align: 'end'
			},
			layout: {
				top: 0,
				bottom: 0,
				right: 50,
				left: 0
			}
	};

	const	unAcknowledgedData = {
		datasets: [{
			data: this.props.unAcknowledgedData.length > 0 ? this.props.unAcknowledgedData.map(data => data.count) : [],
			backgroundColor: [
				'rgba(255, 99, 132, 0.8)',
				'rgba(54, 162, 235, 0.8)',
			],
			borderColor: [
				'rgba(255, 99, 132, 1)',
				'rgba(54, 162, 235, 1)'
			],
		}],

		// These labels appear in the legend and in the tooltips when hovering different arcs
		labels: this.props.unAcknowledgedData.length > 0 ? this.props.unAcknowledgedData.map(data => data.acknowledge ? '':'') : []
	};
	
	const unAcknowledgeOptions = {
			responsive: true,
			animation: {
				animateScale: true,
				animateRotate: true
			},
			tooltips: {
				titleFontSize: 15,
				bodyFontSize: 15,
				intersect: false
			},
			legend: {
				display: true,
				labels: {
					fontSize: 15,
					boxWidth: 25
				}
			},
	};

		return (
			<div className='row report-detail'>
				<div className="col-md-5 grid-margin stretch-card report-detail event_type">
					<div className="card report-detail event_type">
						<div className="card-body report-detail event_type">
							<h4 className="card-title report-detail event_type">{this.handleChangeServiceName(this.props.service)}    </h4>
							<div className='report-detail-card-chart event_type'>
								{this.props.eventTypeData.length > 0 ? <Bar data={eventTypeData} options={eventTypeOptions} id='detail-report-event_type'/>: <h5 className='empty-chart-data'> </h5>}
							</div>
						</div>
					</div>
				</div>
				<div className="col-md-3 grid-margin stretch-card report-detail severity">
					<div className="card report-detail severity">
						<div className="card-body report-detail severity">
							<h4 className="card-title report-detail severity">{this.handleChangeServiceName(this.props.service)}    </h4>
							{this.props.severityData.length > 0 ?
									<div className='report-detail-card-chart severity'>
										<Pie data={severityData} options={severityOptions} id='detail-report-severity'/>
										<div className='report-detail-chart-description-severity'>
											{
												this.props.severityData.length === 4 ?
													<div className='severity_description2_detail_report'>
														{
															this.props.severityData.sort((a, b) => a.event_type_severity - b.event_type_severity).map((severityItem) => {
																return (
																	<span className={severityItem.event_type_severity === 0 ? 'report-detail-chart-description-severity-info':(severityItem.event_type_severity === 1 ? 'report-default-chart-description-severity-minor':(severityItem.event_type_severity === 2 ? 'report-default-chart-description-severity-major':(severityItem.event_type_severity === 3 ? 'report-default-chart-description-severity-critical':'')))}>{severityItem.event_type_severity === 0 ? 'Info : ':(severityItem.event_type_severity === 1 ? 'Minor : ':(severityItem.event_type_severity === 2 ? 'Major : ':(severityItem.event_type_severity === 3 ? 'Critical : ':''))) }{severityItem.count > 0 ? severityItem.count.toString().replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",") + '' : '0'}</span>
																)
															})}
													</div>
													:
													this.props.severityData.length > 1 ?
														<div className='severity_description2_detail_report'>
															<span className='report-detail-chart-description-severity-info'>Info : {this.props.severityData.some((severityItem) => severityItem.event_type_severity === 0)? this.props.severityData.find((severityItem) => severityItem.event_type_severity === 0).count > 0 ? (this.props.severityData.find((severityItem) => severityItem.event_type_severity === 0).count.toString().replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",") + '') : '0': '0'}</span>
															<span className='report-detail-chart-description-severity-minor'>Minor : {this.props.severityData.some((severityItem) => severityItem.event_type_severity === 1)? this.props.severityData.find((severityItem) => severityItem.event_type_severity === 1).count > 0 ? (this.props.severityData.find((severityItem) => severityItem.event_type_severity === 1).count.toString().replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",") + '') : '0': '0'}</span>
															<span className='report-detail-chart-description-severity-major'>Major : {this.props.severityData.some((severityItem) => severityItem.event_type_severity === 2)? this.props.severityData.find((severityItem) => severityItem.event_type_severity === 2).count > 0 ? (this.props.severityData.find((severityItem) => severityItem.event_type_severity === 2).count.toString().replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",") + '') : '0': '0'}</span>
															<span className='report-detail-chart-description-severity-critical'>Critical : {this.props.severityData.some((severityItem) => severityItem.event_type_severity === 3)? this.props.severityData.find((severityItem) => severityItem.event_type_severity === 3).count > 0 ? (this.props.severityData.find((severityItem) => severityItem.event_type_severity === 3).count.toString().replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",") + '') : '0': '0'}</span>
														</div>
														:
														this.props.severityData.sort((a, b) => a.event_type_severity - b.event_type_severity).map((severityItem, index) => {
															return(
																<div className='severity_description2_detail_report'>
																	<span className='report-detail-chart-description-severity-info'>Info : {this.props.severityData.some((severityItem) => severityItem.event_type_severity === 0)? this.props.severityData.find((severityItem) => severityItem.event_type_severity === 0).count > 0 ? (this.props.severityData.find((severityItem) => severityItem.event_type_severity === 0).count.toString().replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",") + '') : '0': '0'}</span>
																	<span className='report-detail-chart-description-severity-minor'>Minor : {this.props.severityData.some((severityItem) => severityItem.event_type_severity === 1)? this.props.severityData.find((severityItem) => severityItem.event_type_severity === 1).count > 0 ? (this.props.severityData.find((severityItem) => severityItem.event_type_severity === 1).count.toString().replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",") + '') : '0': '0'}</span>
																	<span className='report-detail-chart-description-severity-major'>Major : {this.props.severityData.some((severityItem) => severityItem.event_type_severity === 2)? this.props.severityData.find((severityItem) => severityItem.event_type_severity === 2).count > 0 ? (this.props.severityData.find((severityItem) => severityItem.event_type_severity === 2).count.toString().replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",") + '') : '0': '0'}</span>
																	<span className='report-detail-chart-description-severity-critical'>Critical : {this.props.severityData.some((severityItem) => severityItem.event_type_severity === 3)? this.props.severityData.find((severityItem) => severityItem.event_type_severity === 3).count > 0 ? (this.props.severityData.find((severityItem) => severityItem.event_type_severity === 3).count.toString().replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",") + '') : '0': '0'}</span>
																</div>
															)})
											}
										</div>
									</div>
										: 
									<h5 className='empty-chart-data'> </h5>
								}
						</div>
					</div>
				</div>
				<div className="col-md-2 grid-margin stretch-card report-detail unAck">
					<div className="card report-detail unAck">
						<div className="card-body report-detail unAck">
							<h4 className="card-title report-detail unAck">{this.handleChangeServiceName(this.props.service)}    </h4>
							<div className='report-detail-card-chart unAck'>
								{this.props.unAcknowledgedData.length > 0 ? <Doughnut data={unAcknowledgedData} options={unAcknowledgeOptions} id='report-detail-unAcknowledge' width={300}/> : <h5 className='empty-chart-data'> </h5>}
								{this.props.unAcknowledgedData.length > 0 ?
									<div className='unAckRate detail'>
										{this.handleUnAckRate(this.props.unAcknowledgedData)}%
									</div>
									:
									''
								}
								{this.props.unAcknowledgedData && this.props.unAcknowledgedData.map((data, index) => {
									if(data.acknowledge === false){
										return (
											<div className={styles.unAck_value} key={index}>
												<label className={styles.unAck_label}></label>
												<span className="text-success ml-2 mb-0 font-weight-large report-detail unAck">{data.count.toString().replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",")}</span>
											</div>
										)
									}
								})}
							</div>
						</div>
					</div>
				</div>				
			</div>
		);
	}
}


export default DetailReport;