				data: this.props.eventTypeData ? this.props.eventTypeData.map(data => data.count) : [],
				backgroundColor: [
					'rgba(54, 162, 235, 0.6)',
					'rgba(255, 206, 86, 0.6)',
					'rgba(153, 102, 255, 0.6)',
					'rgba(255, 159, 64, 0.6)',
					'rgba(75, 192, 192, 0.6)',
					'rgba(255, 99, 132, 0.6)',
					'rgba(175, 89, 134, 0.6)',
					'rgba(215, 199, 34, 0.6)',
					'rgba(39, 149, 75, 0.6)',
					'rgba(8, 41, 115, 0.6)',
					'rgba(236, 41, 75, 0.6)',
					'rgba(136, 141, 92, 0.6)',
					'rgba(76, 241, 192, 0.6)',
					'rgba(16, 211, 92, 0.6)',
					'rgba(46, 11, 192, 0.6)',
					'rgba(196, 11, 192, 0.6)',
					'rgba(86, 111, 42, 0.6)',
					'rgba(26, 251, 72, 0.6)',
				],
